/**
 * Main application page.
 */

'use strict';

var app              = require('mag-app'),
    keys             = require('stb-keys'),
    Footer           = require('mag-component-footer'),
    Panel            = require('mag-component-panel'),
    PanelSet         = require('mag-component-panel-set'),
    LayoutList       = require('mag-component-layout-list'),
    ModalMessage     = require('mag-component-modal'),
    RadioList        = require('mag-component-radio-list'),
    ScrollBar        = require('stb-component-scrollbar'),
    ScrollArea       = require('mag-component-scroll-area'),

    appExit          = require('../modules/app.exit'),
    webInput         = require('../modules/web.input'),
    previewImage     = require('../modules/preview.bookmark'),
    intents          = require('../modules/intents'),
    validateUrl      = require('../modules/validate.url'),
    div              = require('../modules/div'),

    page             = new (require('stb-component-page'))({$node: window.pageMain}),

    browser          = core.plugins.browser,
    messagesToEngine = browser.messages.toEngine,
    messagesToApp    = browser.messages.toApp,

    isBrowserInit    = false,
    bookmarks        = [],
    doc              = document,

    browseMessageReload,
    mainPanelList,
    headerAmount,
    navigationListData,
    settingsListData,
    progressBar,
    mainPanelSet,
    mainPanel,
    rightPanel,
    leftPanelMenu,
    mainPanelTitle,
    mainPanelListScroll,
    rightInfoPanelScroll,
    modalMessageScroll,
    mediaActionsModalList;


/**
 * Close already opened modal windows and set appropriate flags.
 */
function closeModalWindow () {
    Object.keys(page.modals).forEach(function ( modal ) {
        page.modals[modal].hide();
    });

    mainPanelSet.focus();
    app.flags.modalActive = false;
    page.footer.show();
}


/**
 * Construct comprehensive information about items in main central panel.
 *
 * @param {Object} data - data source
 */
function constructInfo ( data ) {
    var nodes = [
        data.title && div('title theme-subhead-color', data.title + ':'),
        div(null, data.text)
    ];

    page.textArea.$body.innerHTML = '';

    nodes.forEach(function ( node ) {
        if ( node ) {
            page.textArea.$body.appendChild(node);
        }
    });

    page.textArea.show();
    page.textArea.init();
}


/**
 * Construct bookmarks list with storage data.
 *
 * @param {string} [excludeUrl] - URL to exclude/remove from bookmarks
 *
 * @return {Object[]} bookmarks as ready to use constructed list
 */
function fillBookmarksList ( excludeUrl )  {
    var storedBookmarks = app.storage.bookmarks,
        index = storedBookmarks.length,
        data = [],
        favicon;

    if ( excludeUrl ) {
        while ( index-- ) {
            // is in bookmarks
            if ( storedBookmarks[index].url === excludeUrl ) {
                // remove from bookmarks
                storedBookmarks.splice(index, 1);
            } else {
                favicon = new Image();
                favicon.src = storedBookmarks[index].favicon;
                data.push({
                    items: [
                        {
                            value: favicon,
                            className: 'bookmarkListFavicon'
                        },
                        {
                            value: storedBookmarks[index].url,
                            className: 'bookmarkListUrl'
                        },
                        {
                            className: 'theme-icon theme-icon-favorite-active active'
                        }
                    ],
                    value: storedBookmarks[index]
                });
            }
        }
    } else {
        while ( index-- ) {
            favicon = new Image();
            favicon.src = storedBookmarks[index].favicon;
            data.push({
                items: [
                    {
                        value: favicon,
                        className: 'bookmarkListFavicon'
                    },
                    {
                        value: storedBookmarks[index].url,
                        className: 'bookmarkListUrl'
                    },
                    {
                        className: 'theme-icon theme-icon-favorite-active active'
                    }
                ],
                value: storedBookmarks[index]
            });
        }
    }

    return data;
}


/**
 * Set new data/new options to panel/list.
 *
 * @param {Object} data - array of station objects for parsing into LayoutList
 * @param {boolean} isDataRaw - flag that shows if given data is only data or whole config
 * @param {Object} [mixin] - some additional properties
 *
 * @return {boolean} result
 */
function reRender ( data, isDataRaw, mixin ) {
    var tmpData;

    if ( isDataRaw ) {
        tmpData = {
            scroll: data.length < 8 ? null : mainPanelListScroll,
            data: data,
            focusIndex: 0,
            cycle: true,
            size: 7
        };

        if ( mixin ) {
            Object.keys(mixin).forEach(function ( item ) {
                tmpData[item] = mixin[item];
            });
        }

        mainPanelList.init(tmpData);

        if ( data.length > 7 ) {
            if ( !mainPanel.scroller ) {
                mainPanel.scroller = true;
                mainPanelListScroll.init({
                    realSize: data.length
                });
                mainPanelListScroll.$node.style.display = '';
            }
        } else {
            mainPanel.scroller = false;
            mainPanelListScroll.$node.style.display = 'none';
        }

        headerAmount.innerHTML = data.length;
    } else {
        mainPanelList.init(data);
        headerAmount.innerHTML = data.data.length;
    }

    return true;
}


/**
 * Action for menu call for using in footer.
 *
 * @param {string} [url] - optional URL for loading
 */
function callMenu ( url ) {
    if ( app.flags.webWindowActive ) {
        app.flags.webWindowActive = false;
        mainPanelSet.show();
        app.addressBar.toggle();
        page.footer.init(page.footer.navigationViewConfig);
        app.flags.favoritesActive && reRender(fillBookmarksList(), true) && constructInfo({title: '', text: ''});
        app.flags.favoritesActive && page.footer.init(page.footer.bookmarksViewConfig);
    } else {
        app.flags.webWindowActive = true;
        mainPanelSet.hide();
        app.addressBar.toggle();
        page.footer.init(page.footer.browserViewConfig);
        browser.sendMessage(messagesToEngine.userSettings, app.storage);
        browser.sendMessage(messagesToEngine.showWebWindow, url);
    }
}


/**
 * Construct appropriate context menu for media resource.
 *
 * @param {Object} config - configuration parameters
 * @param {Object} config.data - media resource metadata
 * @param {boolean} config.playable - can media resource be played
 *
 * @return {Object} data for init list
 */
function constructListContent ( config ) {
    var data = [
        {
            items: [
                {
                    className: 'theme-icon theme-icon-download'
                },
                gettext('Download')
            ],
            click: function () {
                closeModalWindow();
                intents.openDownloads({
                    title: config.data.itemName,
                    uri:   config.data.itemLink
                }, callMenu);
            }
        }
    ];

    if ( config.playable ) {
        data.unshift({
            items: [
                {
                    className: 'theme-icon theme-icon-play'
                },
                gettext('Play')
            ],
            click: function () {
                closeModalWindow();
                intents.startPlayback({
                    mime:  config.data.itemMime,
                    title: config.data.itemName,
                    uri:   config.data.itemLink
                }, callMenu);
            }
        });
    }

    return {
        data:       data,
        size:       data.length,
        focusIndex: 0
    };
}


/**
 * Set or remove item from bookmarks collection.
 *
 * @param {Object} data - bookmark details
 */
function toggleFavorites ( data ) {
    var index;

    for ( bookmarks = app.storage.bookmarks, index = bookmarks.length; index--; ) {
        // is in bookmarks
        if ( bookmarks[index].url === data.url ) {
            // remove from bookmarks
            bookmarks.splice(index, 1);
            webInput.fillStar(app.addressBar.favorites, false);

            return;
        }
    }

    webInput.fillStar(app.addressBar.favorites, true);
    bookmarks.push({
        url: data.url,
        favicon: app.addressBar.favicon.src,
        title: data.title
    });
}


/**
 * Render menu on top of the page.
 */
// eslint-disable-next-line
