                    var currentState = app.storage.settings.isVirtualMouse;

                    app.storage.settings.isVirtualMouse = !currentState;
                    $virtualMouseInfo.innerText = settingsListData.data[1].items[2].value = currentState ? gettext('Off') : gettext('On');
