/**
 * Main page render. Do basic initialization stuff here.
 */
function renderPage () {
    progressBar = webInput.setProgressBar({className: 'progressBar'});
    page.modals = {};
    page.footer = new Footer({parent: page});

    page.footer.browserViewConfig = {
        left: {
            code: keys.menu,
            action: function () {
                browser.sendMessage(messagesToEngine.hideWebWindow);
            }
        },
        middle: [
            {
                code: keys.f1,
                title: gettext('Reload'),
                action: function () {
                    browser.sendMessage(messagesToEngine.reload);
                }
            },
            {
                code: keys.keyboard,
                title: gettext('Virtual keyboard'),
                action: function () {
                    browser.sendMessage(messagesToEngine.openVirtualKeyboard);
                }
            },
            {
                code: keys.f3,
                title: gettext('Favorites'),
                action: function () {
                    toggleFavorites({url: app.data.currentUrl, title: app.data.currentUrlTitle});
                }
            },
            {
                code: keys.f4,
                title: gettext('Address bar'),
                action: function () {
                    browser.sendMessage(messagesToEngine.search);
                }
            }
        ],
        right: {
            code: keys.frame,
            action: function () {
                browser.sendMessage(messagesToEngine.setFullScreen);
            }
        }
    };

    page.footer.navigationViewConfig = {
        left: {
            code: keys.menu,
            action: callMenu
        }
    };

    page.footer.bookmarksViewConfig = {
        left: {
            code: keys.menu,
            action: callMenu
        },
        middle: [
            {
                code: keys.f3,
                title: gettext('Favorites'),
                action: function () {
                    var activeItem = mainPanelList.$focusItem;

                    if ( app.flags.favoritesActive && activeItem ) {
                        // re-render list with new bookmarks data
                        reRender(fillBookmarksList(activeItem.data.value.url), true);
                        app.storage.bookmarks.length || (page.textArea.$body.innerHTML = '');
                    }
                }
            }
        ]
    };

    page.footer.settingsViewConfig = {
        left: {
            code: keys.menu,
            action: callMenu
        }
    };

    page.add(progressBar);
    page.footer.init(page.footer.browserViewConfig);
    page.footer.show();

    app.addressBar = {
        faviconBlock: doc.getElementById('addressBarFavicon'),
        loader: doc.getElementById('loader'),
        favicon: doc.getElementById('favicon'),
        placeholder: 'Enter url address or text to search...',
        url: doc.getElementById('addressBar'),
        progress: progressBar,
        favorites: doc.getElementById('addressBarFavorites'),
        toggle: function () {
            this.faviconBlock.classList.toggle('hidden');
            this.url.classList.toggle('hidden');
            this.favorites.classList.toggle('hidden');
            this.progress.$node.classList.toggle('hidden');
        }
    };

    renderMenu();
}


page.once('show', renderPage);

page.addListener('keydown', function ( event ) {
    switch ( event.code ) {
        case keys.back:
            (app.flags.modalActive ? closeModalWindow : callMenu)();
            break;
        case keys.ok:
            app.flags.favoritesActive && rightPanel.$node.classList.contains('active') && mainPanelList.emit('click:item');
            break;
    }
});


/**
 * Callback function for handling messages from browser engine.
 * There are many gSTB API usages in handling logic, so main work happens in corresponding plugin.
 * So here we can add some additional non-primary logic.
 *
 * @param {Object} event - event object
 */
browser.onMessageReceive = function ( event ) {
    var itemPlayable = false,
        index, progressLevel, mimeType;

    switch ( event.message ) {
        case messagesToApp.back:
            if ( app.intent ) {
                app.intent.close();
            } else {
                appExit.goBack();
            }

            break;
        case messagesToApp.home:
            appExit.goHome();
            break;
        case messagesToApp.search:
            intents.openKeyboard({
                title: app.addressBar.placeholder,
                value: app.data.currentUrl
            },
            function ( keyboardEvent ) {
                var data = keyboardEvent.value;

                // sanitize URL
                if ( !validateUrl(data) ) {
                    data = 'https://www.google.com/search?hl=' + core.environment.language + '&q=' + data;
                }

                if ( !app.flags.ignoreMessages ) {
                    browser.sendMessage(messagesToEngine.userSettings, app.storage);
                    browser.sendMessage(messagesToEngine.userTextInput, data);
                }
            });
            break;
        case messagesToApp.mediaAvailable:
            mimeType = event.data.itemMime.split('/')[0];
            itemPlayable = ['audio', 'video'].indexOf(mimeType) !== -1;
            event.data.itemName = decodeURIComponent(event.data.itemName);

            if ( itemPlayable ) {
                event.data.itemMime = 'content/' + mimeType;
            }

            mediaActionsModalList.init(constructListContent({
                data:     event.data,
                playable: itemPlayable
            }));

            page.modals.mediaAvailable.show();
            break;
        case messagesToApp.reload:
            browseMessageReload(browseMessageReload.initUrl, browseMessageReload.currentUrl = event.data);
            break;
        case messagesToApp.openMenu:
            callMenu();
            break;
        case messagesToApp.changeUrl:
            // full-fill user history of visited pages
            app.addressBar.url.innerText = app.data.currentUrl = event.data.url;
            app.data.currentUrlTitle = event.data.title;
            // reset visual favorites state
            webInput.fillStar(app.addressBar.favorites, false);

            for ( index = app.storage.bookmarks.length; index--; ) {
                if ( app.storage.bookmarks[index].url === app.data.currentUrl ) {
                    webInput.fillStar(app.addressBar.favorites, true);
                    break;
                }
            }

            break;
        case messagesToApp.setLoadProgress:
            progressLevel = Number(event.data);
            app.addressBar.progress.set(progressLevel === 100 ? 0 : progressLevel);
            break;
        case messagesToApp.setFavicon:
            app.addressBar.favicon.onload = function () {
                app.addressBar.loader.classList.add('hidden');
                app.addressBar.favicon.classList.remove('hidden');
            };
            app.addressBar.favicon.onerror = function () {
                app.addressBar.favicon.classList.add('noFavicon');
                app.addressBar.loader.classList.add('hidden');
                app.addressBar.favicon.classList.remove('hidden');
            };
            app.addressBar.favicon.src = webInput.setFavicon(app.data.currentUrl);
            break;
        case messagesToApp.unsetFavicon:
            app.addressBar.favicon.classList.add('hidden');
            app.addressBar.favicon.classList.remove('noFavicon');
            app.addressBar.loader.classList.remove('hidden');
            break;
        case messagesToApp.favoritesState:
            toggleFavorites(event.data);
            break;
        case messagesToApp.notification:
            core.notify({
                title: event.data,
                icon: 'info',
                type: 'error'
            });
            break;
        case messagesToApp.openTaskManager:
            callMenu();
            intents.openTaskManager(callMenu);
            break;
        case messagesToApp.openSettings:
            intents.openSystemSettings(callMenu);
            break;
        case messagesToApp.ready:
            browser.sendMessage(messagesToEngine.userSettings, app.storage);
            break;
    }
};

core.addListener('intent', function ( intent, callback ) {
    var context = {};

    if ( intent.action === 'browse' ) {
        // error
        if ( !intent.data || !intent.data.uri ) {
            callback(true, null);

            return;
        }

        if ( intent.events && intent.events.reload ) {
            browseMessageReload = intent.events.reload[0];
            browseMessageReload.initUrl = intent.data.uri;
        }

        if ( isBrowserInit ) {
            browser.sendMessage(messagesToEngine.intentUrl, intent.data.uri);
        } else {
            console.log('Browser invoked by intent.....');
            isBrowserInit = true;
            browser.init({data: ['page=' + intent.data.uri], purpose: 'parameter'});
        }

        app.intent = intent;

        Object.defineProperty(context, 'uri', {
            get: function () {
                return browseMessageReload.currentUrl || browseMessageReload.initUrl;
            }
        });

        callback(false, context);
    }
});

if ( !isBrowserInit ) {
    browser[browser.standalone ? 'initStandalone' : 'initRegular']();
}

core.addListeners({
    show: function () {
        app.flags.ignoreMessages = false;

        if ( app.flags.webWindowActive ) {
            browser.sendMessage(messagesToEngine.showWebWindow);
        }
    },
    hide: browser.showPortalWindow
});

module.exports = page;
